import React, { useState } from 'react';
import DeleteIcon from '@material-ui/icons/Delete';
import CreateIcon from '@material-ui/icons/Create';
import './index.css';

const Datacomp = (props) =>{
    const [line,setLine] = useState(false);


    const cut = () =>{
        setLine(true)
    }

   

    return(
        <>
            <li className="Ul-lists-item" style={{textDecoration: line ?"line-through": "none"}}>{props.text} 
                <span className="float-right">
                    <span onClick={cut}>
                        <input type="checkbox" className="inter-inpu"/>
                    </span>
                    <CreateIcon className="pensil"/>
                    <DeleteIcon className="dltom" onClick={()=>{
                        props.onSelect(props.id)
                    }}/>
                </span>
            </li><br/> 
        </>
    )
}

export default Datacomp;