import React, { useState } from "react";
import './index.css';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import LibraryBooksIcon from '@material-ui/icons/LibraryBooks';
import Datacomp from './Datacomp';



const App = () => {
  const [kelly,chgkelly] = useState();
  const [herry,chgherry] = useState([]);


  const hool = (mooj) =>{
    chgkelly(mooj.target.value)

  }

  const listofitm = (soom) =>{

    
    

    chgherry((olditem)=>{
      return [...olditem,kelly];
    });

    chgkelly(" ");

    soom.preventDefault();

  }

  const deletitmes = (id) =>{
    chgherry((olditem)=>{
      return olditem.filter((elemntos,indexos)=>{
        return indexos !== id ;
      })
    })
  }


  return(
      <>
        <div className="main-div my-4"> 
          <h2 className="text-center my-4">TodoInput</h2>
          <div className="forms-1">
            <form>
                {/* <input type="text"/> */}
                <div className="input-group mb-3">

                  <div className="input-group-prepend">
                    <span className="input-group-text inpu1"><LibraryBooksIcon className="pt-2"/></span>
                  </div>

                  <input type="text" className="form-control" placeholder="New Todo" onChange={hool} value={kelly}  name="data"/>
                  
                </div>
                <button className="add-new-btn" onClick={listofitm}> Add New Task</button>
            </form>

           
          </div>

          <div className="buttn-sec my-3">
              <h2 className="text-center">TodoInput</h2>
                <div className="buttn-sec2 my-3">
                  <buton className="room3">All</buton>
                  <buton className="room4">Done</buton>
                  <buton className="room3">Todo</buton>
                </div>
          </div>

          <div className="Ul-lists my-5">
              <ul>
                  
                  {
                    herry.map((val,indo)=>{
                        return <><Datacomp key={indo} id={indo} text={val} onSelect={deletitmes}/> </>
                    })
                  }
              </ul>
          </div>

          <div className="two-botton">
              <button className="main-dele-btn">Delete done Task</button>
              <button className="main-dele-btn">Delete all Task</button>
          </div>

        </div>
      </>
  )
}

export default App;